import { mapState } from "vuex";
import uuidv1 from "uuid/v1";
export default {
  name: "App",

  components: {},

  data() {
    return {
      left: true,
      mini: false,
      link: "now",
      textshow: false,
      title: "",
      text: "",
      searchText: ""
    };
  },
  methods: {
    showMenu() {
      if (this.left) {
        this.mini = !this.mini;
      } else {
        this.left = !this.left;
      }
    },
    checkInput() {
      if (this.title !== "") {
        this.textshow = true;
      } else {
        this.textshow = false;
      }
    },
    clearAll() {
      this.textshow = false;
      this.text = "";
      this.title = "";
    },
    addNote() {
      const note = {
        title: this.title,
        text: this.text,
        id: uuidv1(),
        state: "running"
      };
      this.todoList.push(note);
      this.$store.commit("SetTasks", this.todoList);
      this.clearAll();
    }
  },
  computed: {
    ...mapState({
      todoList: state => {
        return state.todoList;
      }
    })
  },
  mounted() {
    if (this.todoList.length == 0) {
      this.$store.commit("initTasks");
    }
  }
};
